.item {
  border: 1px solid royalblue;
  padding: 40px;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  cursor: pointer;
  font-size: 25px;
  max-width: 30px;
  width: 100%;
  height: 30px;
  text-transform: uppercase;
}
