.winner{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.winner button{
    max-width: 100px;
    width: 100%;
    height: 30px;
    margin-top: 15px;
    background-color: white;
    border: 1px solid black;
    cursor: pointer;
    font-weight: 500;
}
.winner button:hover{
    background-color: black;
    color: white;
}