.score{
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
}
.score-item{
    padding: 10px;
}
.score h1{
    font-size: 20px;
}
.score-item.border{
    border: 1px solid royalblue;
    border-radius: 7px;   
}
@media screen and (max-width:400px){
    .score{
        width: 30%;
        justify-content: start;
        flex-direction: column;
    }
}